/* Dark theme styles for the "terms-labor" section */
.terms-labor {
    background-color: #1a1a1a;
    color: #ffffff;
    padding: 20px;
    margin: 210px auto;
    border-radius: 10px;
  }
  
  .terms-labor h1 {
    color: #e6e6e6;
  }
  
  .terms-labor p {
    margin: 10px 0;
  }
  
  .terms-labor a {
    color: #ff0000;
    text-decoration: none;
  }
  
  .terms-labor a:hover {
    text-decoration: underline;
  }
  